// Bootstrap Popover
$(function () {
    $('[data-toggle="popover"]').popover({
        trigger: 'hover',
        template: '<div class="popover" role="tooltip"><div class="popover-arrow"></div><div class="popover-content"></div></div>'
    })
});

$(".alert").fadeTo(3000, 500).slideUp(200, function(){
    $(".alert").alert('close');
});

$(function () {
    $('[data-toggle="tooltip"]').tooltip()
});